import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import loginSide from "../assets/images/ins-img.png"; // Image for side section
import logo from "../assets/images/logo.svg"; // Logo image
import LoginForm from "./LoginForm"; // Import the LoginForm
import { Link } from "react-router-dom";

// Logo Section Component (Left Side)
const LoginSideImage = () => (
  <div className="LoginFormWrp__left">
    <img src={loginSide} alt="Login Side" />
    <div className="LoginFormWrp__logo">
      <img src={logo} alt="logo" />
    </div>
  </div>
);

// Main Login Component
const InstructorLogin = () => (
  <Container>
    <Row>
      <Col lg={12}>
        <div className="LoginFormWrp d-flex">
          <LoginSideImage />
          <div className="LoginFormWrp__right InstructorLogin__right">
            <div>
            <h2>Instructor Login</h2>
            <p>
              Want to become an Instructor with Us? 
              <Link to="/registration"> Register Now!</Link>
            </p>
            </div>
            <LoginForm />
          </div>
        </div>
      </Col>
    </Row>
  </Container>
);

export default InstructorLogin;
